<template>
  <form @submit.prevent="updateEgr" >
            <div>

                <input
                    type='text'
                    name='descripcion'
                    class='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    v-model="editEgreso.descripcion"

                />
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="editEgreso.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                
                >
                Actualizar

                </button>

            </div>
        </form>
        <button class="btn btn-success mt-5" @click="salirEditFerney" >Salir</button>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from 'vuex'
export default {
    data(){
        return{
            editEgreso:{}
        }
    },
    mounted(){
        this.editEgreso = this.getEgresoFerney
    },
    computed:{
        ...mapGetters('ferneyEgreso',['getEgresoFerney']),
    },
    methods:{
     ...mapActions('ferneyEgreso',['updateEgresoFerney']),
     ...mapMutations('ferneyEgreso',['salirEditFerney']),

     async updateEgr() {
            
            await this.updateEgresoFerney(this.editEgreso)
       }
    }
}
</script>

